import React from 'react';
import { Input, Select } from 'antd';
import { order as fieldName } from '../../../../const/fieldName';
import { width180 } from '../../../../const/style';

const { Option } = Select

export default [
  { name: fieldName.userName, item: <Input placeholder="" style={width180} /> },
  { name: 'orderNo', item: <Input placeholder="" style={width180} /> },
  {
    name: fieldName.orderStatus, item: <Select placeholder="" style={width180} >
      <Option value='1'></Option>
      <Option value='2'></Option>
      <Option value='3'></Option>
      <Option value='4'></Option>
      <Option value='5'></Option>
      <Option value='6'></Option>
    </Select>
  },
];



