import React from 'react'
import { order as fieldName } from '../../../../const/fieldName';

export default [
  { title: '', dataIndex: fieldName.userName, width: 140 },
  { title: '', dataIndex: 'orderNo', width: 180 },
  {
    title: '',
    dataIndex: fieldName.orderStatus,
    width: 140,
    render: orderStatus => {
      switch (orderStatus) {
        case "1":
          return <span></span>
        case "2":
          return <span></span>
        case "3":
          return <span></span>
        case "4":
          return <span></span>
        case "5":
          return <span></span>
        case "6":
          return <span></span>
        default:
          return <span></span>
      }
    }
  },
  { title: '', dataIndex: fieldName.logisticsName },
  { title: '', dataIndex: fieldName.logisticsNo, width: 160 },
  { title: '', dataIndex: fieldName.paymentTime, width: 180 },
];



